<script>
import { omit } from 'ramda';
import StatusLoad from '@components/Loads/StatusLoad';
import { createNamespacedHelpers } from 'vuex';
import formatDate from '@utils/filters/formatDate';
import unslugify from '@utils/filters/unslugify';
import capitalize from '@utils/filters/capitalize';
import formatInspectionChecklistValue from '@utils/filters/formattedInspectionChecklistValue';
import Logo from '@components/Logo';
import PhotoGallery from '@components/PhotoGallery/PhotoGallery.vue';
import { actions as inspectionActions } from '@modules/inspection/constants';
import { actions as loadActions } from '@modules/loads/constants';
import DamageCodeDefinitionsList from '@modules/inspection/components/DamageCodeDefinitionsList';
import PageNotFoundContent from '@components/PageNotFoundContent';

const {
  mapActions: mapInspectionActions,
  mapState: mapInspectionState,
} = createNamespacedHelpers('inspection');

const {
  mapActions: mapLoadsActions,
  mapState: mapLoadsState,
} = createNamespacedHelpers('loads');

export default {
  name: 'Inspection',
  components: {
    DamageCodeDefinitionsList,
    PageNotFoundContent,
    PhotoGallery,
    StatusLoad,
    Logo,
  },
  props: {
    loadId: {
      type: String,
      required: true,
    },
  },
  data() {
    return {
      loading: true,
    };
  },
  filters: {
    formatInspectionChecklistValue,
    capitalize,
    formatDate,
    unslugify,
  },
  methods: {
    omit,
    getPhotos(inspection, inspectionType, photosType) {
      const type = photosType === 'photos' ? 'damages'.toUpperCase() : 'source'.toUpperCase();
      const vin = String(inspection.vehicle.vin).toUpperCase();
      const photos = inspection[inspectionType][photosType];

      return photos.length ? photos.map((url, index) => ({
        url,
        filename: `${vin}-${type}-${index}`,
      })) : [];
    },
    getInspectionChecklistValueVClass(value) {
      switch (true) {
        case typeof value === 'boolean' && value:
          return 'success--text';
        case typeof value === 'boolean' && !value:
          return 'color-red--text';
        default:
          return 'primary-dark--text';
      }
    },
    ...mapInspectionActions({
      getDamages: inspectionActions.GET_DAMAGES,
    }),
    ...mapLoadsActions({
      getLoad: loadActions.GET_LOAD_INSPECTIONS,
      getLoadVehicleInspections: loadActions.GET_LOAD_VEHICLE_INTERNAL_INSPECTIONS,
    }),
  },
  computed: {
    ...mapInspectionState(['damages']),
    ...mapLoadsState(['load', 'inspections']),
  },
  async mounted() {
    try {
      this.loading = true;
      await this.getLoad({ id: this.loadId });
      await this.getDamages();
      await this.getLoadVehicleInspections({ loadId: this.loadId });
    } catch (error) {
      // TODO: log error - console.log(error);
    } finally {
      this.loading = false;
    }
  },
};
</script>

